#question{
    background-color:white;
    border-radius:20px;
    max-width:800px;
    padding:2rem;
}

#question h2 {
    margin-bottom: 1rem;
    color: #1c1c1c;
    font-size: 20px;
}

#question p {
   color: #8435de;
}
@media (max-width: 430px) {
    #question {
        max-width:auto;
        padding:1rem;
}#question h2 {
    margin-bottom: 1rem;
    color: #1c1c1c;
    font-size: 19px;
}#question p {
    font-size: 20px;
}
}