.options {
    background-color: #3c0e70;
    border-radius: 0.9rem;
    padding: 10px;
    margin-bottom:1rem;
    cursor: pointer;
    transition: 0.4s;
}

.options:hover {
    opacity:0.8;
}

.correct{
    background-color: #1ae413;
    font-weight:bold;
}
.wrong{
    background-color:#fd0404;
    opacity:0.9;
}
@media (max-width: 430px) {
    .options {
        padding: 2px;
        margin-bottom:5px;
}
}